.coaches {
  padding: 0 0 89px 0;

  h2 {
    font-weight: 600;
    font-size: 54px;
    line-height: 64.8px;
    letter-spacing: 0.03em;
    font-family: $ff-alumni-sans;
    color: $color-sapphire;
    margin: 0 0 48px 0;
  }
}

.coaches__slide {
  position: relative;
  width: max-content;

  h3 {
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
    color: $color-default-white;
    margin: 0;
    z-index: 2;
    position: absolute;
    text-align: center;
    width: 100%;
    bottom: 7%;
    left: 0;
  }

  &:hover {
    h3 {
      bottom: 73%;
    }

    .coaches__description {
      opacity: 1;
    }
  }
}

.coaches__description {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: 1;
  background-color: $color-sapphire-opacity;
  padding: 75px 30px 30px 30px;
  height: 100%;

  span {
    display: block;
    font-weight: 400;
    color: $color-default-white;
    margin: 0 0 33px 0;
    text-align: center;
  }

  li {
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    margin: 0 0 13px 0;
    font-family: $ff-roboto-condensed;
    color: $color-default-white;
  }
}

@include vp-1199 {
  .coaches {
    padding: 0 0 100px 0;

    h2 {
      font-size: 42px;
      line-height: 50.4px;
      margin: 0 0 30px 0;
      text-align: center;
    }
  }

  .coaches__slide {
    &:hover {
      h3 {
        bottom: 7%;
      }
  
      .coaches__description {
        opacity: 0;
      }
    }

    &.is-active {
      h3 {
        bottom: 73%;
      }
  
      .coaches__description {
        opacity: 1;
      }
    }    
  }
}

@include vp-767 {
  .coaches {
    padding: 0 0 70px 0;

    h2 {
      font-size: 32px;
      line-height: 38.4px;
      margin: 0 0 41px 0;
    }
  }
}
